%clearfix {
  &::after {
    content: "";
    display: block;
    clear: both;
  }
}

.test-mixin {
  @extend %clearfix;
  background-color: yellow;
}

.left_box, .right_box {
  height: 300px;
  width: 300px;
  border: 1px solid black;

}

.left_box {
  float: left;
}

.right_box {
  float: right;

}

%ext-container {
  height: 100px;
  width: 500px;
  margin: 10px auto;
  border: 1px solid;
}

.success {
  @extend %ext-container;
  background: rgba(red, 0.5);
  border-color: red;
}

.error {
  @extend %ext-container;
  background: rgba(blue, 0.5);
  border-color: blue;
}

.info {
  @extend %ext-container;
  background: rgba(yellow, 0.5);
  border-color: yellow;
}